<template>
  <div id="adjuntos">
    <v-data-table
        :headers="headers"
        :items="dataAdjuntos"
        class="elevation-1"
    >
      <template v-slot:item.descripcionestado="{ item }">
        <span :class="[{ColorVBFalse:(item.estado === 'REZ')}, {ColorVBTrue:(item.estado !== 'REZ')}]">{{ item.descripcionestado }}
        </span>
      </template>
      <template v-slot:item.action="{ item }">
        <v-icon
          class="mr-5"
          color="primary"
          @click="guardarEstado(item)"
          v-if="estadoproceso != 'Finalizado'"
        >
          save
        </v-icon>
      </template>
      <template v-slot:item.valida="{ item }">
        <v-autocomplete
            v-model="item.nuevoestado"
            :items="estados"
            item-text="texto"
            item-value="id"
            hide-details
            :cache-items="false"
            :readonly="estadoproceso == 'Finalizado'"
        >
        </v-autocomplete>
      </template>
      <template v-slot:item.observacion="{ item }">
        <v-text-field
            v-model="item.observacion"
            :readonly="estadoproceso == 'Finalizado'"
        ></v-text-field>
      </template>
      <template v-slot:item.carguearchivo="{ item }">
        <v-chip
            v-if="item.file_path"
            class="ma-2"
            @click="openInNewTab(item.file_path, item.file_type)"
            color="green darken-2"
            text-color="white"
            style="margin: 5px"
        >
            {{item.file_name.substring(0, 50)}}
        </v-chip>
      </template>
    </v-data-table>
  </div>
</template>
<script>
import { anunciosProcAdquisicion, adminApp } from '@/services/api'
import FileExchange from '../../../services/FileExchange'
import { b64toBlobUrl } from '../../../services/utilities'
export default {
  props: ['idregistro', 'tipodoc', 'idpropconv', 'estadoproceso'],
  data () {
    return {
      dataAdjuntos: [],
      estados: [
        { id: 'VAL', texto: 'VALIDADO' },
        { id: 'REZ', texto: 'RECHAZADO' }
      ],
      headers: [
        { text: 'Descripción', align: 'left', value: 'descripciondocumento', class: 'subtitle-1 font-weight-bold black--text' },
        { text: 'Estado Actual', align: 'left', sortable: false, value: 'descripcionestado', class: 'subtitle-1 font-weight-bold black--text' },
        { text: 'Archivo', align: 'left', sortable: false, value: 'carguearchivo', class: 'subtitle-1 font-weight-bold black--text' },
        { text: 'Validación', align: 'left', sortable: false, value: 'valida', class: 'subtitle-1 font-weight-bold black--text' },
        { text: 'Observación', align: 'left', sortable: false, value: 'observacion', class: 'subtitle-1 font-weight-bold black--text', width: 450 },
        { text: 'Guardar', align: 'center', sortable: false, value: 'action', class: 'subtitle-1 font-weight-bold black--text' }
      ]
    }
  },
  async mounted () {
    this.getData()
  },
  methods: {
    async getData () {
      this.dataAdjuntos = []
      const { data } = await anunciosProcAdquisicion.buscarAdjuntosDocumentoContratoRegistro({
        registro: this.idregistro,
        tipo: this.tipodoc,
        idpropconv: this.idpropconv
      })
      this.dataAdjuntos = data.archivos
    },
    async openInNewTab (url, type) {
      try {
        const result = await FileExchange.getFileFromStorage(url)
        const fileData = result.data
        const urlBlob = b64toBlobUrl(fileData, type, '')
        var win = window.open(urlBlob, '_blank')
        win.focus()
      } catch (error) {
        console.log(error)
      }
    },
    async guardarEstado (item) {
      const { data } = await adminApp.validarDocumentosContrato({
        registro: item.registro,
        proconvdoc: item.proconvdoc,
        estado: item.nuevoestado,
        observacion: item.observacion,
        idpropconv: this.idpropconv,
        tipodoc: this.tipodoc
      })
      if (data === '1' || data === 1) {
        this.$toasted.success('Proceso realizado con éxito', { icon: 'done', duration: 5000 })
      } else {
        this.$toasted.error('Error desconocido', { icon: 'error', duration: 5000 })
      }
      this.getData()
    }
  },
  computed: {
    adjuntos: {
      get () {
        return this.dataAdjuntos
      },
      set (val) {
        this.adjuntos = val
      }
    }
  },
  watch: {
    idregistro (val) {
      this.dataAdjuntos = []
      this.getData()
    },
    tipodoc (val) {
      this.getData()
    },
    idpropconv (val) {
      this.getData()
    }
  }
}
</script>
<style>
  .anexosClass {
    min-width: 300px;
  }
  #adjuntos .v-label {
    font-size: .70rem;
  }
  #adjuntos .v-label .inFiles{
    font-size: .95rem;
  }
  .ColorVBTrue{
    color:blue;
    font-weight: bold;
  }
  .ColorVBFalse{
    color:red;
    font-weight: bold;
  }
</style>
